import React from "react";
// import FileViewer from "react-file-viewer";
import SubPage from "@/components/SubPage";
require("./style.scss");

const FileView = (props: any) => {
  const {
    history,
    location: { state = {} }
  } = props;
  const { type, url } = state || {};
  const onError = (err: any) => {
    console.log("预览出错", err);
  };
  return (
    <SubPage history={history} className="fileView-page" title="文件预览" bgColor="#eee">
      {/* <FileViewer fileType={type} filePath={url} errorComponent={<div>出错了</div>} onError={onError} /> */}
    </SubPage>
  );
};

export default FileView;
